<script setup lang="ts">
import { useI18n } from "vue-i18n";
import type { PaginationState } from "types/common/pagination";
import { onMounted } from "vue";
import { useLandingPageStore } from "@user-web/stores/modules/landing-page";
import { AutoCompleteReturnParamTypes } from "@enums/search/search.enum";
import AllServices from "@user-web/views/auth/LandingPage/components/MainSection/AllServices.vue";
import { PagePaths } from "@enums/routes/user-web.routes";

const { t } = useI18n();
const landingPageStore = useLandingPageStore();

const searchPages = async (search: string) => {
  landingPageStore.isSearchingForPages = true;
  await landingPageStore.searchForCityOrPage(search, AutoCompleteReturnParamTypes.PAGES);
  landingPageStore.isSearchingForPages = false;
};

const loadMore = async ($paginationState: PaginationState) => {
  if (landingPageStore.pagesPagination?.nextUrl) {
    await landingPageStore.getMorePageNames($paginationState);
  }
};

onMounted(async () => {
  await landingPageStore.getPageNames(100);
});
</script>

<template>
  <AllServices
    v-if="landingPageStore.pageNames?.length"
    :title="t('generics.all_items', { items: t('global.page', 2) })"
    :search-placeholder="t('page.search_pages')"
    :subtitle="`${landingPageStore.pagesPagination?.totalCount} ${t('generics.connected_item', {
      item: t('global.page', 2),
    })}`"
    :empty-state-description="
      t('generics.no_found_items', { item: t('global.cities_or_municipalities') })
    "
    type="pages"
    :service-names="landingPageStore.pageNames"
    :routing-path="PagePaths.HOME[1]"
    @infinite="loadMore"
    @search="searchPages"
  />
</template>
